<template>
  <div>
    <h3 style="text-align: center; font-size: 50px;">로그인</h3>
    <table style="margin: auto; width: 40%;">
      <tbody>
        <tr>
          <td width="40%">아이디</td>
          <td>
            <input
              type="text"
              :value="id"
              @input="changeId"
              style="width: 90%; font-size: 20px;"
            />
          </td>
        </tr>
        <tr>
          <td>비밀번호</td>
          <td>
            <input
              type="password"
              :value="pwd"
              @input="changePwd"
              style="width: 90%; font-size: 20px;"
            />
          </td>
        </tr>
        <tr>
          <td></td>
          <td style="text-align: center;"> <!-- text-align 속성 추가 -->
            <!-- 로그인 버튼 추가 -->
            <button @click="login">로그인</button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  props: {
    id: String,
    pwd: String,
  },
  methods: {
    changeId(event) {
      this.$emit('changeId', event.target.value);
    },
    changePwd(event) {
      this.$emit('changePwd', event.target.value);
    },
    // 실제 로그인 기능 추가
    login() {
      // 여기에 실제 로그인 기능을 구현합니다.
      // 백엔드와 통신하거나, 로컬 상태를 확인하여 로그인 여부를 결정합니다.
      // 아래는 간단한 예시, 실제로는 백엔드와의 통신 등이 필요할 수 있습니다.
      if (this.id === "cbkim" && this.pwd === "1111") {
        alert("로그인 성공!");
        this.$emit('login'); // 로그인 성공 시 이벤트 발생
      } else {
        alert("아이디 또는 비밀번호가 올바르지 않습니다.");
      }
    },
  },
};
</script>
